.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}

.product__info {
    height: 100px;
    margin-bottom: 15px;
}

.product__price {
    margin-top: 5px;
}

.product__rating {
    display: flex;
}

.product > img {
    object-fit: contain;
    max-height: 200px;
    width: 100%;
    margin-bottom: 15px;
}

.product > button {
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
}

@media only screen and (max-width: 600px) {
    .product {
      margin: 10px 0px 10px 0px;
      padding: 20px 0px 20px 0px;
      text-align: center;
    }
  
    .product__rating {
      display: flex;
      align-items: center;
      justify-content: center;
      padding-top: 5px;
    }
  }